@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@800&display=swap);
* {
  margin: 0;
  padding: 0;
  background-color: black;
}

h1 {
  color: #e0e0e0;
  font-family: "Roboto Slab", serif;
  letter-spacing: 0.1em;
  text-align: center;
  margin-top: 30px;
  font-size: 2.5rem;
}

.disable {
  pointer-events: none;
}

.gridContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 65vh;
}

.gridItem {
  background-color: rgba(211, 211, 211, 0.842);
  border-radius: 10px;
  border: 1px solid black;
  width: calc(100vw / 100);
  margin: calc(100vw / 320);
}

.buttonsPanel {
  display: flex;
  justify-content: center;
  margin-top: 3vh;
}

.btn {
  display: inline-block;
  padding: 0.35em 1.2em;
  border: 0.1em solid #ffffff;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.12em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 1rem;
  color: #ffffff;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s;
  outline: none;
}

.btn:hover {
  color: #000000;
  background-color: #ffffff;
}

.btn-selected {
  color: purple;
  border: 0.2em solid purple;
  background-color: #ffffff;
  font-weight: bolder;
}
.resetButon {
  margin-right: 20vw;
}

@media (max-width: 770px) {
  .btn {
    font-size: 0.8rem;
  }
  .resetButon {
    margin-right: 8vw;
  }
}

